.deposit-modal {
  $primary: #71FF3A;
  $white: #FFF;
  $warn: #FFE626;
  $error: #FF1744;

  @keyframes DepositModalGlowing {
    0% {
      filter:
        drop-shadow(1px -1px 2px rgba($primary, .3))
        drop-shadow(-1px 1px 1.5px rgba($white, .3))
        ;
    }

    25% {
      filter:
        drop-shadow(1px 1px 1px rgba($primary, .5))
        drop-shadow(-1px -1px 3px rgba($white, .4))
        ;
    }

    50% {
      filter:
        drop-shadow(-1px 1px 2px rgba($primary, .7))
        drop-shadow(1px -1px 1.5px rgba($white, .3))
        ;
    }

    75% {
      filter:
        drop-shadow(-1px -1px 3px rgba($primary, .5))
        drop-shadow(1px 1px 1px rgba($white, .2))
        ;
    }

    100% {
      filter:
        drop-shadow(1px -1px 2px rgba($primary, .3))
        drop-shadow(-1px 1px 1.5px rgba($white, .3))
        ;
    }
  }

  .glowing-1 {
    animation: DepositModalGlowing 4s linear -2s infinite;
  }

  .glowing-2 {
    animation: DepositModalGlowing 4s linear 0s infinite;
  }
}
